import { ProjectReportId, ReportText, Title } from '../value';

import {
  BaseProjectReport,
  BaseUpdateProjectReport,
  IBaseProjectReport,
  IUpdateBaseProjectReport,
} from './BaseProjectReport';

export interface IProjectMeetingReport extends IBaseProjectReport {
  title: Title;
  reportText: ReportText;
}
export interface IUpdateProjectMeetingReport extends IUpdateBaseProjectReport {
  title?: Title;
  reportText?: ReportText;
}

export class ProjectMeetingReport extends BaseProjectReport {
  readonly title: Title;
  readonly reportText: ReportText;

  constructor(args: IProjectMeetingReport) {
    super(args);
    this.title = args.title;
    this.reportText = args.reportText;
  }

  update(args: IUpdateProjectMeetingReport): UpdateProjectMeetingReport {
    return new UpdateProjectMeetingReport({ id: this.id, ...args });
  }
}

export class UpdateProjectMeetingReport extends BaseUpdateProjectReport {
  readonly title?: Title;
  readonly reportText?: ReportText;

  constructor(args: IUpdateProjectMeetingReport & { id: ProjectReportId }) {
    super(args);
    this.title = args.title;
    this.reportText = args.reportText;
  }
}
